import React from 'react';
import Login from './login/login.jsx';
import Signup from './login/signup.jsx';
import Home from './main/home.jsx';
import AddRecipe from './add-recipe/add-recipe.jsx';
import { HashRouter as Router, Switch, Route } from 'react-router-dom';
import './App.css';

function App() {
    return (
		<Router>	
			<div className='App'>
				<div className='App-header'>
					<Switch>
						<Route path='/login'>
							<Login/>
						</Route>
						<Route path='/signup'>
							<Signup/>
						</Route>
						<Route path='/addrecipe'>
							<AddRecipe/>
						</Route>
						<Route path='/'>
							<Home/>
						</Route>
					</Switch>
				</div>
			</div>
		</Router>
    );
}

export default App;
