import React from 'react';
import './login-styles.css';

class Signup extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            email: '',
            unHashedPass: ''
        }

        this.submitMethod = this.submitMethod.bind(this);
    }

    async submitMethod () {
        document.getElementById('errorText').innerHTML = 'Waiting ... ';
        try {
            const response = await fetch('https://dinner-server.herokuapp.com/Signup', {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json',
                    'Access-Control-Allow-Origin': '*'
                },
                body: JSON.stringify(this.state)
            });
            const result = await response.json();
            if (result.statusCode === 200) {
                document.getElementById('errorText').innerHTML = '';
                window.location.href = '/foodie/#login';
            }
            else {
                if (result.info) {
                    document.getElementById('errorText').innerHTML = result.info;
                }
                else {
                    document.getElementById('errorText').innerHTML = "Sorry, but the Foodie servers encountered an unknown error. Please send your request again, or contact the developer";
                }
            }
        } catch (e) {
            document.getElementById('errorText').innerHTML = "Sorry, but the Foodie servers encountered an unknown error. Please send your request again, or contact the developer";
        }
    }
    
    render() {
        return (
            <div className='login-wrapper'>
                <h2>Signup for Foodie</h2>
                <p id='errorText'></p>
                <label htmlFor='username' className='form-block'>
                    <span>Email</span>
                    <input type='text' name='username' id='username' value={this.state.email} onChange={event => this.setState({ email: event.target.value })}/>
                </label>
                <label htmlFor='password' className='form-block'>
                    <span>Password</span>
                    <input type='password' name='password' id='password' value={this.state.unHashedPass} onChange={event => this.setState({ unHashedPass: event.target.value})}/>
                </label>
                <input id='need-bottom-spacing' type='submit' onClick={this.submitMethod} value='Signup'/>
                <a className='link link-small' href='/signup'>Login instead</a>
            </div>
        );
    }
}

export default Signup;