import React from 'react';
import './home.css';
import Card from './recipe-card/card.jsx';

class Home extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            recipe: '',
            waiting: false
        };

        this.getNewRecipe = this.getNewRecipe.bind(this);
        this.capitalize = this.capitalize.bind(this);
    }
    componentDidMount () {
        if (localStorage.getItem('token')) this.getNewRecipe();
        else window.location.href = '/foodie/#login';
    }
    capitalize (string) {
        let letters = string.split("");
        letters[0] = letters[0].toUpperCase();
        for (let i = 1; i < letters.length; i++) {
            if (letters[i] === " " || letters[i] === "-") {
                letters[i + 1] = letters[i + 1].toUpperCase();
            }
        }
        return letters.join("");
    }
    async getNewRecipe () {
        if (!this.state.waiting) {
            this.setState({waiting: true});

            const response = await fetch(`https://dinner-server.herokuapp.com/DinnerSelection?token=${localStorage.getItem('token')}`, {
            method: 'GET',    
            headers: {
                    'Access-Control-Allow-Origin': '*'
                }
            });
            const result = await response.json();

            this.setState({recipe: this.capitalize(result.food), waiting: false});
            document.querySelector('.card-div').classList.add('changed-recipe');
            setTimeout(() => {
                document.querySelector('.card-div').classList.remove('changed-recipe');
            }, 150);
        }
    }
    render() {
        return(
            <div className='home-container'>
                <div className='main-wrapper' id='cardContainer'>
                    <Card recipe={this.state.recipe}></Card>
                </div>
                <div className='button-wrapper'>
                    <button className='big-button' onClick={this.getNewRecipe}>Get a Different Recipe</button>
                    <a href='/foodie/#addrecipe'>
                        <button className='big-button'>Add More Recipes</button>
                    </a>
                </div>
            </div>
        );
    }
}

export default Home;