import React from 'react';
import './add-recipe.css';

class AddRecipe extends React.Component {
    constructor(props) {
        super(props);
        
        this.state = {
            newRecipe: ''
        };

        this.handleSubmit = this.handleSubmit.bind(this);
    }
    componentDidMount () { if (!localStorage.getItem('token')) window.location.href = '/foodie/#login'; }
    async handleSubmit () {
        document.getElementById('waitingText').innerHTML = 'Waiting ...';
        const response = await fetch ('https://dinner-server.herokuapp.com/AddRecipe', {
            method: "POST",
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Origin': '*',
            },
            body: JSON.stringify({
                token: localStorage.getItem('token'),
                recipeName: this.state.newRecipe
            })
        });

        const result = await response.json();
        document.getElementById('waitingText').innerHTML = '';

        if (result.statusCode === 401) window.location.href = '/foodie/#login';
        else {
            alert('Item added successfully');
            window.location.href = '/foodie/#';
        }
    }
    render() {
        return (
            <div className='addrecipe-wrapper'>
                <h2>Add a New Recipe to Your Profile</h2>
                <p id='waitingText'></p>
                <label htmlFor='newrecipe'>
                    <input name='newrecipe' id='newrecipe' type='text' value={this.state.newRecipe} onChange={event => this.setState({newRecipe: event.target.value})}/>
                </label>
                <input type='submit' value='Add the new recipe' onClick={this.handleSubmit}/>
            </div>
        );
    }
}

export default AddRecipe;